.articles-page {
    height: 100vh;
    width: 100%;
    @include flexColumn;
    h1 {
        margin: 1em 0;
        @include tablette-version {
            margin-top: 3em;
        }
    }
    overflow: hidden;
    .articles-container {
        @include flexColumn;
        overflow-y: auto;
        overflow-x: hidden;
    }
    .article-container {
        position: relative;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        h4,p,h5 {
            color: black;
        }
        padding: 1em;
        margin: 1em 0;
        width: 80%;
        border-radius: 1em;
        transition: .8s;
        transform: translateX(0);
        &.hide {
            &.left {
                transform: translateX(-100%);
            }
            &.right {
                transform: translateX(100%);
            }
        }
        h4 {
            align-self: center;
            font-size: 1.3em;
        }
        button p {
            color: white;    
        }
    }
    .paragraph {
        margin: 1em 0;
        height: 6em;
        overflow: hidden;
        &.visible {
            height: auto;
        }
        p {
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .comment-container {
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        margin: .8em 0;
        border: solid .2em black;
        padding: .5em;
        h5 {
            margin-right: .5em;
            font-size: 1.2em;
            align-self: flex-start;
        }
        .delete-button {
            right: .5em;
        }
        .modif-button {
            right: 3em;
        }
    }
    .blank {
        width: 100%;
        background-color: #fff;
        height: 2em;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        p {
            font-weight: bold;
            margin-right: 1em;
            cursor: pointer;
        }
    }
    input,label {
        color: black;
    }
    form {
        flex: 1;
        display: flex;
        align-items: center;
    }
    input {
        margin: 0 .5em;
        flex: 1;
        padding: .3em;
    }
    .modify-delete {
        display: flex;
        padding: 0 .5em;
        div {
            margin: 0 .1em;
        }
    }
    .image-container {
        width: 90%;
        @include flexCenter;
        img {
            @include imageFit;
        }
    }
}