.user-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    button {
        margin: .5em;
    }
}

.user-inputs {
    margin: .5em;
    width: 70vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    input {
        width: 84%;
        color: black;
        padding: .2em;
        font-size: 1em;
    }
    label {
        font-size: 1.2em;
        max-width: 6em;
    }
    .validity {
        position: absolute;
        right: -4vw;
        display: none;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        width: 2em;
        height: 2em;
    }
    &.valid {
        .validity {
            display: flex;
            background-color: green;
            &::before {
                @include icon('\f00c',1.2em);
            }
        }
    }
    &.invalid {
        .validity {
            display: flex;
            background-color: rgb(223, 0, 0);
            &::before {
                @include icon('\f00d',1.2em);
            }
        }
    }
    
}

.password-force {
    margin-top: 1em;
    display: flex;
    align-items: center;
    width: 70vw;
    p {
        font-size: 1.2em;
        max-width: 6em;
        margin-right: .4vw;
    }
}
.lights-container {
    width: 84%;
    display: flex;
    align-items: center;
    div {
        flex: 1;
        height: .5em;
        background-color: #565656;
    }
    div:first-child {
        border-radius: 1em 0 0 1em;
    }
    div:last-child {
        border-radius: 0 1em 1em 0;
    }
    .low {
        background-color: red;
    }
    .mid {
        background-color: rgb(255, 157, 0);
    }
    .high {
        background-color: rgb(0, 208, 0);
    }
}

@include tablette-version {
    .validity {
        bottom: 0;
    }
    .user-inputs {
        width: 90vw;
        flex-direction: column;
        label {
            width: 84%;
            max-width: initial;
            margin-bottom: .5em;
        }
    }
    .password-force {
        p {
            width: 84%;
            max-width: initial;
            margin-bottom: .5em;
        }
        flex-direction: column;
        width: 90vw;
        margin-bottom: 5em;
    }
}