.dark .switch-button {
    border-color: white;
    &::before {
        transform: translateX(1.3em);
        background-color: #fff;
    }
}

.back {
    z-index: 99;
    position: absolute;
    width: 30px;
    height: 30px;
    top: .5em;
    left: .5em;
    &::before {
        @include icon('\f3e5',2em);
        @include tablette-version {
            font-size: 6vw;
        }
    }
    &.hide {
        display: none;
    }
}

button {
    cursor: pointer;
    background-color: black;
    color: #fff;
    border: solid;
    border-radius: 1em;
    padding: .5em 1em;
    font-size: 1em;
}

.delete-button,.modif-button {
    background-color: black;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    @include flexCenter;
}

.delete-button {
    &::before {
        @include icon('\f00d',1em);
    }
}

.modif-button {
    &::before {
            @include icon('\f044',1em);
    }
}

.arrow-up {
    position: fixed;
    width: 5em;
    height: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    right: .5em;
    bottom: .5em;
    cursor: pointer;
    &::before {
        @include icon('\f139',2.5em);
    }
}