.create-aticle-page {
    min-height: 100vh;
    @include flexColumn;
    width: 80%;
    h1 {
        margin: 1em 0;
    }
    form {
        width: 100%;
    }
    .article-inputs {
        margin: .5em 0;
        display: flex;
        justify-content: space-between;
    }
    .buttons-container {
        width: 100%;
        @include flexCenter;
    }
    button {
        margin: 0 1em;
    }
    input,textarea {
        min-width: 90%;
        color: black;
    }
    input {
        padding: 0 .5em;
    }
    textarea {
        padding: .5em;
        min-height: 20vh;
        max-width: 90%;
    }
    label {
        font-size: 1.5em;
    }
    .image-container {
        width: 90%;
        img {
            @include imageFit;
        }
    }
}