.home-page {
    @include flexColumn;
    overflow: hidden;
}
.more-arrow {
    position: fixed;
    bottom: 1em;
    left: 1em;
    &::before {
        @include icon('\f0ab',2em);
        animation: flashing-arrow 6s infinite;
    }
}
.title-container {
    @include flexCenter;
    height: 100vh;
}
.presentation {
    margin: 2em 0;
    padding: 0 10%;
    @include flexColumn;
    h2 {
        margin-bottom: 1em;
    }
    transform: translateX(-100%);
    transition: .8s;
    &.appears {
        transform: translateX(0);
    }
}
.footer {
    margin-bottom: 2em;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 0 10%;
    h3 {
        font-size: 1.5em;
    }
    transform: translateX(100%);
    transition: .8s;
    &.appears {
        transform: translateX(0);
    }
}